.card {
    width: 280px;
    font-size: 14px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    cursor: pointer;
}

.card:hover {
    opacity: 0.7;
}

.img {
    width: 100%;
    height: 180px;
    object-fit: cover;
}
.price{

    font-size: 20px;

}
.name{
    font-size: 20px;
}
.addButton{
    background-color: #D1E0DD;
    font-weight: 400;
    border: none;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size:20px;

    border-radius: 4px;
    cursor: pointer;
    color: #2A324B;
    margin-bottom: 10px;

}